








import { Component, Vue, Watch } from "vue-property-decorator";
@Component({
  components: {}
})
export default class Logs extends Vue {
  private logs: string[];

  public constructor() {
    super();
    this.logs = this.$store.state.outputs.lityLogs;
  }

  @Watch("logs")
  scrollToBottom() {
    this.$nextTick().then(() => {
      const elem = this.$refs.logs as any;
      elem.scrollIntoView(false);
    });
  }
}
